@import "colors";

.nav {
    width: 100%;
    height: 80px;
    position: relative;
    top: 0;
    display: flex;
    flex-direction: column;
    background-color: transparent;
}

.nav-container {
    display: flex;
    justify-content: space-between;
    height: 100%;
    align-items: center;
}

.nav-spacing {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-left: 6.9%;
    padding-right: 6.9%;
}

.nav-left {
    display: flex;
    align-items: center;

}

.divider {
    background-color: #7B7B7B;
    height: 1px;
    width: 100%;
}